import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import Interactable from './Interactable.jsx';
import { Timeline } from './Timeline.jsx';

import 'components/EditList.css'

class EditList extends React.Component {
    static propTypes = {
        time: PropTypes.arrayOf(PropTypes.object)
    };

    constructor(props) {
        super(props);

        this.handleClick = this.handleClick.bind(this);
    }
    
    render() {
        return (
            <div className='d-flex flex-row'>
                <div className='sidebar'>
                    <div className='up'>
                        
                        <div className='d-flex flex-column box'>
                            <button className='label a' onClick={this.handleClick}></button>
                            <button className='label b'></button>
                            <button className='label c'></button>
                            <button className='label d'></button>
                            <button className='label e'></button>
                            <button className='label f'></button>
                        </div>
                    </div>
                    <div className='down'>
                        
                        <div className='d-flex flex-column box'>
                            <button className='label g'></button>
                            <button className='label h'></button>
                            <button className='label i'></button>
                            <button className='label j'></button>
                            <button className='label k'></button>
                            <button className='label l'></button>
                        </div>
                    </div>
                </div>
                <div className='right'>
                    <div className='board d-flex'>
                        <div className='info'></div>
                        <div className='anim'></div>
                        <div className='button'></div>
                    </div>
                    <div className='timeline'>
                        <Timeline/>
                        <Interactable draggable={true} draggableOptions={draggableOptions}>
                            <div className='label a block'></div>
                        </Interactable>
                        <Interactable draggable={true} draggableOptions={draggableOptions}>
                            <div className='label b block' style={{left: "70px"}}></div>
                        </Interactable>
                    </div>
                </div>
            </div>
        );
    }

    handleClick(e){ // add chosen block to timeline

    }
}

export default connect(state => ({
    ...state.editList
}))(EditList);

const draggableOptions = {
    onmove: event => {
        const target = event.target;
        
        // keep the dragged position in the data-x/data-y attributes
        const x = (parseFloat(target.getAttribute("data-x")) || target.style.left) + event.dx;
       
        if (x > 0) {
            // translate the element
            target.style.left = `${x}px`;
            // target.style.webkitTransform = target.style.transform = "translate(" + x + "px, " + y + "px)";
        }

        // update the posiion attributes
        target.setAttribute("data-x", x);
    }
};